.product_Container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  transition: all .1s ease-in-out;
  max-width: 500px;
  flex: 0 0 auto;
}

.product_Container:hover {
transform: scale(1.1);
}

.product_Container > img{
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}
